/* 整体页面 */
.overview-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

              /* 设置整体背景图片 */
  background-image: url(https://yixinsheng31.github.io/xilingPreserve1.0/assets/header.jpg);
  background-size: cover;
  background-position: center;

  }
  /* 可通过伪元素添加半透明覆盖 */
.overview-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.8); /* 白色半透明遮罩 */
  z-index: -1;
}
  
  .overview-content {
    flex: 1;
    padding: 20px;
  }
  
  /* 顶部区域 */
  .top-section {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 40px;


  }
  
  .video-container {
    flex: 1;
    min-width: 300px;
    margin-right: 20px;
  }
  
  .image-container {
    flex: 1;
    min-width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image-container img {
    max-width: 100%;
    height: auto;
  }
  
  /* 底部区域：三个板块 */
  .bottom-section {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .bottom-section > div {
    flex: 1;
    min-width: 250px;
    background-image: url(https://yixinsheng31.github.io/xilingPreserve1.0/assets/header.jpg);
    background-size: cover;
    background-position: center;
    border: 2px solid #ccc;
    border-radius: 8px;
    padding: 10px;
  }
  
  /* 音频板块 */
  .audio-panel .audio-items {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-around;
  }
  
  /* 文档板块：横向滚动 */
  .documents-panel .documents-cards {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding-bottom: 10px;
    justify-content: space-around;
  }
  
  /* 知识网络板块 */
  .knowledge-panel .knowledge-content {
    padding: 10px;
  }
  